/* Reset CRA demo styles; implement screen layout */

.screen {
  display: grid;
  grid-template-rows: 48px 1fr 96px;
  height: 100vh;
  max-height: 100vh;
}

.tabs {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  border-bottom: 2px solid #000;
}

.tab {
  border: none;
  background: #fff;
  font-weight: 700;
  color: #0a9edc;
  border-right: 2px solid #000;
}
.tab:last-child {
  border-right: none;
}
.tab.active {
  background: #f2f2f2;
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.empty {
  color: #888;
}

.preview .filename {
  color: #333;
  font-weight: 600;
}

.preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.previewImage {
  max-width: min(90vw, 600px);
  max-height: 60vh;
  object-fit: contain;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
}

.resultList {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
}

.resultItem {
  color: #333;
  font-weight: 600;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.uploadBtn {
  border: 3px solid #0a9edc;
  color: #0a9edc;
  background: transparent;
  border-radius: 2px;
  padding: 16px 32px;
  font-weight: 700;
}
